<template>
  <el-dialog :append-to-body="true" title="新增商品列表" :visible.sync="commodityShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="商品名称：">
            <el-input style="width: 203px" v-model="searchList.productName" clearable placeholder="商品名称" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="商品类目：">
            <el-cascader
              v-model="searchList.shopCategoryIdArr"
              :options="category"
              :props="{ checkStrictly: true }"
              clearable></el-cascader>
          </el-form-item>
          <el-form-item label="商品状态：">
            <el-select v-model="searchList.status" clearable>
              <el-option label="出售中" value="1"></el-option>
              <el-option label="待出售" value="0"></el-option>
              <el-option label="已下架" value="2"></el-option>
              <el-option label="回收站" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table
        ref="multipleTable"
        :data="list"
        tooltip-effect="dark"
        style="width: 100%" 
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="商品图片" width="140">
          <template slot-scope="scope">
            <el-popover placement="right" title trigger="hover">
              <el-image
                slot="reference"
                :src="scope.row.productImgUrl"
                :alt="scope.row.productImgUrl"
                style="max-height: 40px;max-width: 40px"
              ></el-image>
              <el-image :src="scope.row.productImgUrl" style="max-height: 350px;max-width: 350px"></el-image>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="商品名称" prop="name"></el-table-column>
        <el-table-column label="分类" prop="productCategoryShopName"></el-table-column>
        <el-table-column label="成本价（元）" width="240" prop="originPrice"></el-table-column>
        <el-table-column label="商品售价（元）" width="240" prop="price"></el-table-column>
        <el-table-column label="商品库存" width="140" prop="sumStock"></el-table-column>
        <el-table-column label="商品状态" width="140" prop="statusName"></el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
      <el-button type="primary" @click="sub">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getProductCategoryShopAllList } from '@/api/commodity/commodityClassify'
import { getVipProductList } from '@/api/memberManage/memberProduct'
export default {
  props: ["commodityShow"],
  watch:{
    commodityShow(newvalue,oldValue){
      if(newvalue){
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        shopCategoryIdArr:[],
        pageNum:1,
        pageSize:5,
        status:''
      },
      category:[],
      list:[],
      listTotal:0,
      multipleSelection:[],           // 勾选的数据
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getCategory()
  },
  methods:{
    sub(){
      console.log('multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择商品后再试');
        return;
      }
      this.$emit('commodityFun',this.multipleSelection)
      this.$refs.multipleTable.clearSelection();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.productName = ''
      this.searchList.shopCategoryIdArr = []
      this.searchList.status = ''
      this.getData();
    },
    // 列表 - 获取数据
    getData(){
      this.fromLoading = true
      this.searchList.shopCategoryPid = this.searchList.shopCategoryIdArr[0] || ''
      this.searchList.shopCategoryId = this.searchList.shopCategoryIdArr[1] || ''
      console.log('请求的数据',this.searchList)
      getVipProductList(this.searchList).then(res=>{
        console.log('vip商品列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
        this.fromLoading = false
      })
    },
    // 获取全部分类
    getCategory(){
      getProductCategoryShopAllList(this.shop.shopId).then(res=>{
        console.log('获取店铺分类',res)
        this.category = res.data
      })
    },
    close(){
      this.$emit('commodityClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>