import request from '@/utils/request'
// 会员商品列表
export function getVipProductInfoList(data) {
  return request({
    url: `/product/business/product/getVipProductInfoList`,
    method: 'post',
    data
  })
}
// 弹框商品列表
export function getVipProductList(data) {
  return request({
    url: `/product/business/product/getVipProductList`,
    method: 'post',
    data
  })
}
// 保存会员商品
export function saveVipProduct(data) {
  return request({
    url: `/product/business/product/saveVipProduct`,
    method: 'post',
    data:data
  })
}
// 删除会员商品
export function deleteVipProduct(data) {
  return request({
    url: `/product/business/product/deleteVipProduct`,
    method: 'post',
    data
  })
}
// 编辑
export function editVipProduct(data) {
  return request({
    url: `/product/business/product/editVipProduct`,
    method: 'post',
    data
  })
}